#pages-nav {
  background-color: #282828;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: fit-content;
  margin: 0 auto 1.5rem;
  padding: .4rem .8rem;
  display: none;
}

#pages-nav.show {
  display: flex;
}

#pages-nav button {
  text-transform: uppercase;
  cursor: pointer;
  background-color: orange;
  border: 0;
  border-radius: 0;
  outline: 0;
  margin: 0;
  padding: .2rem 1rem;
  font-size: .8em;
  font-weight: 400;
}

#pages-nav button:focus, #pages-nav button:not(:disabled):hover {
  background-color: #ff0;
  outline: 0;
  box-shadow: 0 0 15px #ff0, 0 0 30px orange;
}

button#pages-nav-first, button#pages-nav-prev {
  border-radius: 15px 0 0 15px;
}

button#pages-nav-next, button#pages-nav-last {
  border-radius: 0 15px 15px 0;
}

button#pages-nav-goto {
  font-weight: 700;
}
